<template>
  <div class="pageContent">
    <el-card class="filter-container" shadow="never">
      <div class="pageHead" v-if="$route.query.tradeType == 1">订单入账</div>
      <div class="pageHead" v-if="$route.query.tradeType == 2">交易手续费退回</div>
      <div class="pageHead" v-if="$route.query.tradeType == 3">充值</div>
      <div class="pageHead" v-if="$route.query.tradeType == 4">佣金退回</div>
      <div class="pageHead" v-if="$route.query.tradeType == 5">邀请奖励退回</div>
      <div class="pageHead" v-if="$route.query.tradeType == 21">交易手续费</div>
      <div class="pageHead" v-if="$route.query.tradeType == 22">佣金</div>
      <div class="pageHead" v-if="$route.query.tradeType == 23">提现手续费</div>
      <div class="pageHead" v-if="$route.query.tradeType == 24">交易退款</div>
      <div class="pageHead" v-if="$route.query.tradeType == 25">提现</div>
      <div class="pageHead" v-if="$route.query.tradeType == 26">邀请奖励</div>
      <div class="flex-row-between mt10">
        <div class="infoFont" v-if="$route.query.dateType == 'month'">
          对账日期：{{$route.query.startDate}} - {{$route.query.endDate}}
        </div>
        <div class="infoFont" v-if="$route.query.dateType == 'day'">
          对账日期：{{$route.query.date}} 00时00分00秒 - {{$route.query.date}} 23时59分59秒
        </div>
        <div class="warningFont">
          共{{$route.query.type == 'in' ? '收入' : '支出'}}{{$route.query.count}}笔 &nbsp;&nbsp;共{{$route.query.type == 'in' ? '收入' : '支出'}}金额 &nbsp;￥{{$route.query.amount}}
        </div>
      </div>
      <div class="mt20">
        <el-table :data="list" style="width: 100%">
          <el-table-column prop="createTime" label="时间"></el-table-column>
          <el-table-column prop="incomeAmount" label="收支类型">
            <template slot-scope="scope">
              <div :data="scope.row.id">
                <el-tag v-if="$route.query.type == 'out'">支出</el-tag>
                <el-tag type="warning" v-if="$route.query.type == 'in'">收入</el-tag>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="description" label="摘要"></el-table-column>
          <el-table-column label="名称">
            <template slot-scope="scope">
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 1">订单入账</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 2">交易手续费退回</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 3">充值</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 4">佣金退回</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 5">邀请奖励退回</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 21">交易手续费</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 22">佣金</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 23">提现手续费</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 24">交易退款</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 25">提现</div>
              <div :data-id="scope.row.id" v-if="$route.query.tradeType == 26">邀请奖励</div>
            </template>
          </el-table-column>
          <el-table-column prop="amount" label="金额"></el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetRecord } from '@/api/assetsManage/account'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      let startDate,endDate;
      if(this.$route.query.dateType == 'day'){
        startDate = this.$route.query.date + ' 00:00:00'
        endDate = this.$route.query.date + ' 23:59:59'
      }
      if(this.$route.query.dateType == 'month'){
        startDate = this.$route.query.startDate
        endDate = this.$route.query.endDate
      }
      this.searchList.startDate = startDate
      this.searchList.endDate = endDate
      this.searchList.settleState2 = 2
      this.searchList.tradeType = this.$route.query.tradeType
      console.log('全部请求的数据',this.searchList)
      getShopAssetRecord(this.searchList).then(res=>{
        console.log('全部交易记录列表',res)
        if(res.code ==200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.businessNumber = ''
      this.searchList.toUser = ''
      this.searchList.time = []
      this.searchList.toUser = ''
      this.searchList.toUser = ''
      this.searchList.toUser = ''
      this.getData()
    },
    goToInfo(e){
      if(e.tradeType == 3){
        this.$emit('changeActive','1');
        return;
      }
      if(e.tradeType == 25){
        this.$router.push({
          path:'/assetsManage/account/cashoutDetail',
          query:{
            id:e.id
          }
        })
        return;
      }
      if(e.tradeType == 6){
        console.log('会员卡开通记录')
        this.$router.push({
          path:'/memberManage/memberCenter',
          query:{
            type:1
          }
        })
        return;
      }
      this.$router.push({
        path:'/order/orderList/orderinfo',
        query:{
          orderId:e.orderId
        }
      })
    },
    goToMybill(e){
      this.$router.push({
        path:'/assetsManage/myBill',
        query:{
          orderId:e.orderId,
          afterSaleId:e.afterSaleId
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>